html {
  min-height: 100vh;
}


@font-face {
  font-family: 'Trojan pro';
  font-style: normal;
  font-weight: 400;
  src: url('./assets/fonts/Trajan\ Pro\ Regular.ttf'); /* IE9 Compat Modes */
  src: local('Open Sans'), local('OpenSans'),
       url('./assets/fonts/Trajan\ Pro\ Regular.ttf') format('embedded-opentype'), /* IE6-IE8 */
       url('./assets/fonts/Trajan\ Pro\ Regular.ttf') format('woff2'), /* Super Modern Browsers */
       url('./assets/fonts/Trajan\ Pro\ Regular.ttf') format('woff'), /* Modern Browsers */
       url('./assets/fonts/Trajan\ Pro\ Regular.ttf') format('truetype'), /* Safari, Android, iOS */
}

@font-face {
  font-family: 'Hacker';
  font-style: normal;
  font-weight: 400;
  src: url('./assets/fonts/HACKED.ttf'); /* IE9 Compat Modes */
  src: local('Open Sans'), local('OpenSans'),
       url('./assets/fonts/HACKED.ttf') format('embedded-opentype'), /* IE6-IE8 */
       url('./assets/fonts/HACKED.ttf') format('woff2'), /* Super Modern Browsers */
       url('./assets/fonts/HACKED.ttf') format('woff'), /* Modern Browsers */
       url('./assets/fonts/HACKED.ttf') format('truetype'), /* Safari, Android, iOS */
}


body {
  margin: 0;
  padding: 0;
  font-family: 'Hacker';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'trajan-pro-3', 'Trajan Pro', Georgia, 'Times New Roman';
}

*:focus {
  outline: none !important;
  -webkit-tap-highlight-color: transparent;
}

.App {
  text-align: center;
}


// below is css loader
.lds-hourglass {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
}
.lds-hourglass:after {
    content: " ";
    display: block;
    border-radius: 50%;
    width: 0;
    height: 0;
    margin: 6px;
    box-sizing: border-box;
    border: 26px solid #fff;
    border-color: #fff transparent #fff transparent;
    animation: lds-hourglass 1.2s infinite;
}
@keyframes lds-hourglass {
    0% {
      transform: rotate(0);
      animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
    }
    50% {
      transform: rotate(900deg);
      animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    }
    100% {
      transform: rotate(1800deg);
    }
}

/* 
##Device = Desktops
##Screen = 1281px to higher resolution desktops
*/

@media (min-width: 1281px) {

  body {
      background: #282c34 url('./assets/img/background-landscape.jpg') no-repeat center center fixed;
      background-size: cover;
  }

}

/* 
##Device = Laptops, Desktops
##Screen = B/w 1025px to 1280px
*/

@media (min-width: 1025px) and (max-width: 1280px) {

  body {
      background: url('./assets/img/background-landscape.jpg') no-repeat center center fixed;
      background-size:cover;
  }

}

/* 
##Device = Tablets, Ipads (portrait)
##Screen = B/w 768px to 1024px
*/

@media (min-width: 768px) and (max-width: 1024px) {

  body {
      background: url('./assets/img/background-portrait.jpg') no-repeat center center fixed;
      background-size:cover;
  }


}

/* 
##Device = Tablets, Ipads (landscape)
##Screen = B/w 768px to 1024px
*/

@media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {

  body {
      background: url('./assets/img/background-landscape.jpg') no-repeat center center fixed;
      background-size:cover;
  }

}

/* 
##Device = Low Resolution Tablets, Mobiles (Landscape)
##Screen = B/w 481px to 767px
*/

@media (min-width: 481px) and (max-width: 767px) {

  body {
      background: url('./assets/img/background-landscape.jpg') no-repeat center center fixed;
      background-size:cover;
  }

}

/* 
##Device = Most of the Smartphones Mobiles (Portrait)
##Screen = B/w 320px to 479px
*/

@media (min-width: 320px) and (max-width: 480px) {

  body {
      background: url('./assets/img/background-portrait.jpg') no-repeat center center fixed;
      background-size: cover;
  }

}