      min-width: 100%;
      min-height: 100%;
      z-index: -1000; } }
  .Home .darkmode {
    opacity: 1; }
  .Home .brightmode {
    opacity: 0.6; }
  .Home .switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px; }
  .Home .switch input {
    opacity: 0;
    width: 0;
    height: 0; }
  .Home .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s; }
  .Home .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
