  .Home input:focus + .slider {
    box-shadow: 0 0 1px #2196F3; }
  .Home input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    transform: translateX(26px); }
  .Home .slider.round {
    border-radius: 34px; }
  .Home .slider.round:before {
