.menu .cn-wrapper {
  font-size: 1em;
  width: 26em;
  height: 26em;
  overflow: hidden;
  position: fixed;
  z-index: 10;
  bottom: -70px;
  left: 50%;
  border-radius: 50%;
  margin-left: -13em;
  -webkit-transform: scale(0.1);
          transform: scale(0.1);
  -webkit-transition: all .3s ease;
  transition: all .3s ease; }

/* class applied to the container via JavaScript that will scale the navigation up */
.menu .opened-nav {
  border-radius: 50%;
  -webkit-transform: scale(1);
          transform: scale(1); }

.toggle {
  border: none;
  background: none;
  color: white;
  text-align: Center;
  font-size: 1.5em;
  padding-bottom: 1em;
  height: 3.5em;
  width: 3.5em;
  background-color: #111;
  position: fixed;
  left: 50%;
  margin-left: -1.75em;
  bottom: -1.75em;
  border-radius: 50%;
  cursor: pointer;
  z-index: 11; }

.toggle-darkmode {
  background-color: white;
  color: #111; }

.toggle:hover,
.toggle:active,
.toggle:focus {
  background-color: #222; }

.toggle-darkmode:hover,
.toggle-darkmode:active,
.toggle-darkmode:focus {
  background-color: white; }

.overlay-inactive {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  opacity: 0;
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
  z-index: 2;
  pointer-events: none; }

/* Class added to the overlay via JavaScript to show it when navigation is open */
.overlay-inactive.overlay-active {
  pointer-events: auto;
  opacity: 1; }

.menu .cn-wrapper li {
  position: absolute;
  font-size: 14px;
  width: 10em;
  height: 10em;
  -webkit-transform-origin: 100% 100%;
          transform-origin: 100% 100%;
  overflow: hidden;
  left: 50%;
  top: 50%;
  margin-top: -1.3em;
  margin-left: -10em;
  -webkit-transition: border .3s ease;
  transition: border .3s ease; }

.menu .cn-wrapper li a {
  display: block;
  font-size: 20px;
  height: 14.5em;
  width: 14.5em;
  position: absolute;
  bottom: -7.25em;
  right: -7.25em;
  border-radius: 50%;
  text-decoration: none;
  color: #fff;
  padding-top: 30px;
  text-align: center;
  -webkit-transform: skew(-50deg) rotate(-70deg) scale(1);
          transform: skew(-50deg) rotate(-70deg) scale(1);
  -webkit-transition: opacity 0.3s, color 0.3s;
  transition: opacity 0.3s, color 0.3s; }

.menu .cn-wrapper li a span {
  font-size: 1.3em;
  opacity: 0.7; }

/* for a central angle x, the list items must be skewed by 90-x degrees
in our case x=40deg so skew angle is 50deg
items should be rotated by x, minus (sum of angles - 180)2s (for this demo) */
.menu .cn-wrapper li:nth-child(1) {
  -webkit-transform: rotate(-10deg) skew(50deg);
          transform: rotate(-10deg) skew(50deg); }

.menu .cn-wrapper li:nth-child(2) {
  -webkit-transform: rotate(30deg) skew(50deg);
          transform: rotate(30deg) skew(50deg); }

.menu .cn-wrapper li:nth-child(3) {
  -webkit-transform: rotate(70deg) skew(50deg);
          transform: rotate(70deg) skew(50deg); }

.menu .cn-wrapper li:nth-child(4) {
  -webkit-transform: rotate(110deg) skew(50deg);
          transform: rotate(110deg) skew(50deg); }

.menu .cn-wrapper li:nth-child(5) {
  -webkit-transform: rotate(150deg) skew(50deg);
          transform: rotate(150deg) skew(50deg); }

.menu .cn-wrapper li:nth-child(odd) a {
  background-color: #a11313;
  background-color: #f44e4e; }

.menu .cn-wrapper li:nth-child(even) a {
  background-color: #a61414;
  background-color: #f45757; }

/* active style */
.menu .cn-wrapper li.active a {
  background-color: #b31515;
  background-color: #f66f6f; }

/* hover style */
.menu .cn-wrapper li:not(.active) a:hover,
.menu .cn-wrapper li:not(.active) a:active,
.menu .cn-wrapper li:not(.active) a:focus {
  background-color: #b31515;
  background-color: #f66f6f; }

.menu .cn-wrapper li:not(.active) a:focus {
  position: fixed;
  /* fix the "displacement" bug in webkit browsers when using tab key */ }

